@keyframes zoom {
    100% {
        transform: scale(1.1);
    }
}

@keyframes opacity {
    100% {
        opacity: 0.8;
    }
}

.countryCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin-bottom: 10px;
    overflow: hidden;
    cursor: pointer;
    width: 393px;
}

.countryCard:focus {
    outline: 3px solid #00695c;
}

@media screen and (min-width: 1200px) {

    .countryCard:nth-child(4) {
        width: 490px;
    }

    .countryCard:nth-child(5) {
        width: 700px;
    }
}

@media screen and (max-width: 1200px) {
    .countryCard {
        width: 49.5%;
    }

    .countryCard:nth-child(4) {
        width: 49.5%;
    }

    .countryCard:nth-child(5) {
        width: 49.5%;
    }
}


@media screen and (max-width: 800px) {
    .countryCard {
        width: 100%;
    }
    .countryCard:nth-child(4) {
        width: 100%;
    }

    .countryCard:nth-child(5) {
        width: 100%;
    }
}


.cardTitle {
    font-size: 1.3rem;
    font-weight: 600;
}

.cardInfoWrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 10px 25px 10px 10px;
    color: #fff;
    z-index: 11;
}

.cardImg {
    height: 263px;
    border-radius: 2px;
    object-fit: cover;
}

.countryCard:hover > .cardImg {
    animation: zoom 1.2s;
    animation-fill-mode: forwards;
}

.countryCard:hover > .cardMask {
    animation: opacity 1.2s;
    animation-fill-mode: forwards;
}

.cardMask {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(0, 0, 0);
    background: linear-gradient(
            0deg,
            rgba(0, 0, 0, 1) 0%,
            rgba(40, 40, 40, 0.4991130241158963) 40%,
            rgba(255, 255, 255, 0) 100%
    );
    opacity: 0.5;
    z-index: 10;
}
